main {
  display: block;
  width: 100%;
  margin: 0rem auto;
  padding: 0rem 0rem;
}

.caption {
  margin-left: auto;
}

a {
  color: teal;
  text-decoration: none;
}

a:hover {
  color: teal;
  text-decoration: underline;
}

.content-section {
  max-width: 1000px;
  min-height: 75vh;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  flex-direction: column;
  align-items: flex-start;
  margin: 0rem auto;
  padding: 0rem 0rem;
}

.hero {
  width: 100%;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
}

.hero-right {
  width: 100%;
  display: flex;
  -webkit-box-pack: flex-end;
  justify-content: flex-end;
  -webkit-box-align: flex-end;
  align-items: center;
}

.hero-left {
  width: 100%;
  display: flex;
  -webkit-box-pack: flex-start;
  justify-content: flex-start;
  -webkit-box-align: flex-start;
  align-items: center;
}

h1 {
  margin-top: .5rem;
  margin-bottom: 1.25rem;
  font-weight: lighter;
  color: #000;
  font-family: "Mansalva", cursive !important;
  font-size: 2rem;
  line-height: 1rem;
}

h2 {
  margin-bottom: 0rem;
  color: #000;
  font-family: "Mansalva", cursive !important;
  font-size: 2.5rem;
  line-height: 2rem;
}

h3 {
  margin-bottom: 0rem;
  color: #000;
  font-family: "Nunito", sans-serif !important;
  font-weight: bold;
  font-size: 1rem;
  line-height: 1rem;
}

h3 span {
  color: rgb(136, 146, 176);
  font-family: "Actor", sans-serif !important;
  font-weight: bold;
  font-size: 1.05rem;
  line-height: 1.5rem;
}

h4 {
  margin-bottom: 0rem;
  color: rgb(136, 146, 176);
  font-family: "Mansalva", cursive !important;
  font-size: 1.5rem;
  line-height: 2rem;
}

h5 {
  margin-bottom: 0rem;
  color: #fff;
  font-family: "Mansalva", cursive !important;
  font-size: 1.5rem;
  line-height: 2rem;
}

h4:hover {
  color: #fff;
}

h4 a {
  color: rgb(30, 30, 30);
}

.hero-image {
  width: 18rem;
}

.hero-item {
  align-self: center;
}

.flex {
  display: flex;
}

.sample-img {
  width: 50rem;
}

.hero .ant-btn {
  background: none;
}

.text-small {
  font-size: 0.8rem;
}

.text-muted {
  color: rgba(85, 86, 95, 0.8) !important;
  margin-bottom: 0.5rem;
}

span {
  font-family: "Nunito", sans-serif !important;
  font-size: 0.95rem;
  line-height: 1.32rem;
}

.ant-divider {
  margin-top: 0rem !important;
}

.anticon {
  font-size: 1rem;
}
